import React, { Component } from 'react';
import ReactMorph from 'react-morph';
import classNames from 'classnames';

import ScreenPanel from './ScreenPanel'

import './App.css';
import Muni_worm_logo from './Muni_worm_logo.svg';

class TrainTracker extends Component {
  render() {
    return (
      <div className="TrainTracker-container">
        <div className="TrainTracker-servedTrains">
          <div className="TrainTracker-bullet">J</div>
          <div className="TrainTracker-bullet">K</div>
          <div className="TrainTracker-bullet">L</div>
          <div className="TrainTracker-bullet">M</div>
          <div className="TrainTracker-bullet">T</div>
        </div>
        <div className="TrainTracker-nextTrain">
          <span className="TrainTracker-nextTrain-header Page-header">
            <span className="TrainTracker-nextTrain-time">4</span> min away: 
          </span>
          <div className="TrainTracker-bullet">N</div>
        </div>
      </div>
    )
  }
}

class Action extends Component {
  render() {
    return (
      <div className={classNames('Action-container', this.props.height, { 'right': this.props.right, 'basic': this.props.basic })}>
        <div className="Action-label">{this.props.label}</div>
        <div className="Action-content">
          {this.props.children}
        </div>
      </div>
    )
  }
}

class Page extends Component {
  render() {
    return (
      <div className={classNames("Page-container", this.props.className)}>
        <div className="Page-header-container">
          <img className="Page-logo" src={Muni_worm_logo}/>
          <div className="Page-columns">
            <div className="Page-leftHeader Page-header">
              <span className="Page-stationName">Embarcadero </span>
              <span className="Page-currentTime">{
                this.props.date ? this.props.date.toLocaleTimeString() : ''
              }</span>
            </div>
            <TrainTracker/>
          </div>
        </div>
        <div className="Page-body-container Page-columns">
          {this.props.children}
        </div>
      </div>
    )
  }
}

class HomePage extends Component {
  render() {
    return (
      <Page className="Page-Home" date={this.props.date}>
        <div className="Page-column Page-left">
          <Action height="three-high" label="A">
            <div className="Action-header clipper">Buy a Clipper Card</div>
            <div className="Action-description">Monthly Passes  Discounted Fares  Reusable  Works on BART + Caltrain</div>
            <div className="Action-price">$3.00</div>
          </Action>
          <div className="ActionSpacer"/>
          <Action label="E" basic>
            <div className="Action-header system">Change Language</div>
          </Action>
        </div>
        <div className="Page-column Page-right">
          <Action height="two-high" label="F" right>
            <div className="Action-header clipper">Reload your Clipper Card</div>
            <div className="Action-description">Tap your card to the reader or press <strong>F</strong></div>
          </Action>
          <Action label="H" right>
            <div className="Action-header muni">Buy Paper Tickets</div>
          </Action>
          <div className="ActionSpacer"/>
          <Action label="J" basic right>
            <div className="Action-header system">Help</div>
          </Action>
        </div>
      </Page>
    )
  }
}

class ClipperPage extends Component {
  render() {
    return (
      <Page className="Page-Clipper" date={this.props.date}>
        <div className="Page-column Page-left">
          <Action label="A">
            <div className="Action-header clipper">Add 1 ride<div className="Action-price">$2.50</div></div>
          </Action>
          <Action label="B">
            <div className="Action-header clipper">Add 2 rides<div className="Action-price">$5.00</div></div>
          </Action>
          <Action label="C">
            <div className="Action-header clipper">Add 10 rides<div className="Action-price">$25.00</div></div>
          </Action>
          <Action label="D">
            <div className="Action-header clipper">Add custom value</div>
          </Action>
          <Action label="E" basic>
            <div className="Action-header system">Show card balance</div>
          </Action>
        </div>
        <div className="Page-column Page-right">
          <Action height="two-high" label="F" right>
            <div className="Action-header clipper">Buy "M" Pass<div className="Action-price">$75.00</div></div>
            <div className="Action-description">Monthly  Muni  Cable Car</div>
          </Action>
          <Action height="two-high" label="H" right>
            <div className="Action-header clipper">Buy "A" Pass<div className="Action-price">$94.00</div></div>
            <div className="Action-description">Monthly  Muni  Cable Car  BART within SF</div>
          </Action>
          <Action label="J" basic right>
            <div className="Action-header system">Help</div>
          </Action>
        </div>
      </Page>
    )
  }
}

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {date: new Date()};
  }

  tick() {
    this.setState({
      date: new Date()
    });
  }

  componentDidMount() {
    this.timerID = setInterval(
      () => this.tick(),
      1000
    );
  }

  componentWillUnmount() {
    clearInterval(this.timerID);
  }

  render() {
    return (
      <div className="App">
        <ScreenPanel>
          <ClipperPage date={this.state.date}/>
        </ScreenPanel>
      </div>
    );
  }
}

export default App;



